import { Button } from 'react-bootstrap';
import { Modal } from 'react-bootstrap';

export function End(props) {
    let { show, confirmed, cancelled } = props

    return (
        <Modal show={show} centered>
            <Modal.Dialog>
                <Modal.Header>
                    <Modal.Title></Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    </Modal.Body>

            </Modal.Dialog>
        </Modal>
    )
}