import { useState } from 'react'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'

import Form from 'react-bootstrap/Form'
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';
import axios from 'axios';
import { useAppDispatch } from '../../store/hooks';
import { setInfo } from '../../store/slices/infoSlice';
import { useRef } from 'react';

export function Intro({ showInfo, setShowInfo,setShowInfo2 }) {
    let dispatch = useAppDispatch()
    let form = useRef(null);

    let startExp = (e) => {

        let formData = new FormData(form.current)
        for (let k of formData.keys()) {
            console.log(k,formData.get(k))
            dispatch(setInfo({ 'key': k, 'value': formData.get(k) }))
        }
        setShowInfo(false);
        setShowInfo2(true)
        return false;
    }

    return (
        <Modal show={showInfo} centered scrollable>
            <Modal.Header>
                <Modal.Title></Modal.Title>
            </Modal.Header>
            <Modal.Body >
                <p></p>
                <Form ref={form}>

                    <Form.Group className='mb-3'>
                        <Form.Label>1	</Form.Label><br></br>
                        <Form.Check value={1} inline type='radio' name='gender' label='' required></Form.Check>
                        <Form.Check value={2} inline type='radio' name='gender' label=''></Form.Check>
                    </Form.Group>
                    <Form.Group className='mb-3'>
                        <Form.Label>2	</Form.Label><br></br>
                        <Form.Check value={1} inline type='radio' name='degree' label='' required></Form.Check>
                        <Form.Check value={2} inline type='radio' name='degree' label=''></Form.Check>
                        <Form.Check value={3} inline type='radio' name='degree' label=''></Form.Check>
                        <Form.Check value={4} inline type='radio' name='degree' label=''></Form.Check>
                    </Form.Group>
                    <Form.Group className='mb-3'>
                        <Form.Label>3	</Form.Label><br></br>
                        <Form.Check value={1} inline type='radio' name='profession' label='' required></Form.Check>
                        <Form.Check value={2} inline type='radio' name='profession' label=''></Form.Check>
                        <Form.Check value={3} inline type='radio' name='profession' label=''></Form.Check>
                        <Form.Check value={4} inline type='radio' name='profession' label=''></Form.Check>
                        <Form.Check value={5} inline type='radio' name='profession' label=''></Form.Check>
                        <Form.Check value={6} inline type='radio' name='profession' label=''></Form.Check>
                        <Form.Check value={7} inline type='radio' name='profession' label=''></Form.Check>

                    </Form.Group>
                    <Form.Group className='mb-3'>
                        <Form.Label>4	</Form.Label><br></br>
                        <Form.Check value={1} inline type='radio' name='income' label='1500' required></Form.Check>
                        <Form.Check value={2} inline type='radio' name='income' label='1500-3000'></Form.Check>
                        <Form.Check value={3} inline type='radio' name='income' label='3000-5000 '></Form.Check>
                        <Form.Check value={4} inline type='radio' name='income' label='5000-8000'></Form.Check>
                        <Form.Check value={5} inline type='radio' name='income' label='8000'></Form.Check>
                    </Form.Group>

                    <Form.Group className={'align-items-baseline mb-3'} as={Row}>
                        <Col xs={7}>
                            <Form.Label >5	</Form.Label>
                        </Col>
                        <Col xs={2}>
                            <Form.Control type='number' name='age' required></Form.Control>
                        </Col>
                        <Col >
                            <Form.Text></Form.Text>
                        </Col>
                    </Form.Group>
                    <Form.Group className={'align-items-baseline mb-3'} as={Row}>
                        <Col xs={7}>
                            <Form.Label >6	</Form.Label>
                        </Col>
                        <Col xs={2}>
                            <Form.Control type='number' name='height' required></Form.Control>
                        </Col>
                        <Col >
                            <Form.Text>cm</Form.Text>
                        </Col>
                    </Form.Group>
                    <Form.Group className={'align-items-baseline mb-3 flex-left'} as={Row}>
                        <Col xs={7}>
                            <Form.Label >7	</Form.Label>
                        </Col>
                        <Col xs={2}>
                            <Form.Control type='number' name='weight' required></Form.Control>
                        </Col>
                        <Col >
                            <Form.Text>kg</Form.Text>
                        </Col>
                    </Form.Group>
                    <Form.Group className='mb-3'>
                        <Form.Label>8	</Form.Label><br></br>
                        <Form.Check value={1}  type='radio' name='exerciseFrequency' label='' required></Form.Check>
                        <Form.Check value={2}  type='radio' name='exerciseFrequency' label='1-3'></Form.Check>
                        <Form.Check value={3}  type='radio' name='exerciseFrequency' label='3-5'></Form.Check>
                        <Form.Check value={4}  type='radio' name='exerciseFrequency' label='5-6'></Form.Check>
                    </Form.Group>
                    <Form.Group className='mb-3'>
                        <Form.Label>9	</Form.Label><br></br>
                        <Form.Check value={1}  type='radio' name='hungryStatus' label='1' required></Form.Check>
                        <Form.Check value={2}  type='radio' name='hungryStatus' label='2'></Form.Check>
                        <Form.Check value={3}  type='radio' name='hungryStatus' label='3() '></Form.Check>
                        <Form.Check value={4}  type='radio' name='hungryStatus' label='4('></Form.Check>
                        <Form.Check value={5}  type='radio' name='hungryStatus' label='5'></Form.Check>
                    </Form.Group>
                </Form>

            </Modal.Body>
            <Modal.Footer>

                <Button onClick={startExp}></Button>
            </Modal.Footer>
        </Modal>
    )


}